<template>
    <div class="vh-animate vh-animate-fade-in vh-text-center">
        <div class="vh-card vh-card-body vh-card-default vh-card-small">
            <div class="vh-margin-top">
                <small class="axs-small">
                    TICKET SERIES NO.
                </small>

                <h2 class="axs-series-number vh-margin-small-top">
                    {{ getNo('starting')}} ~ {{ getNo('ending')}}
                </h2>
            </div>

            <div class="vh-margin-medium-top">
                <small class="axs-small">
                    TOTAL TICKETS
                </small>

                <h2 class="axs-series-number vh-margin-small-top vh-margin-remove-bottom">
                    {{ getNo('total')}}
                </h2>
            </div>

            <div class="vh-margin-top">
                <vh-button
                type="button"
                :size="'small'"
                :color="'warning'"
                @click="toggleModal('form', true)">
                EDIT
            </vh-button>
            </div>
        </div>

        <div class="vh-margin-medium-top">
            <small class="axs-small">
                NEXT SERIES NO.
            </small>

            <h3 class="axs-release-number vh-margin-small-top">
                {{ nextStartingSeries }} ~ {{ nextEndingSeries }}
            </h3>
        </div>

        <div class="vh-margin-top">
            <vh-button
                class="vh-inline vh-margin-small-right"
                type="button"
                :size="'small'"
                :color="'danger'"
                :outline="true"
                @click="changeToPreviousSeries()">
                PREVIOUS SERIES
            </vh-button>
            <vh-button
                class="vh-inline"
                type="button"
                :size="'small'"
                :color="'primary'"
                @click="changeToNextSeries()">
                NEXT SERIES
            </vh-button>
        </div>

        <form-modal />
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

import { Toast } from '@/utils';

import FormModal from './modals/form';

export default
{
    name: 'Tickets',

    components:
    {
        FormModal,
    },

    data()
    {
        return {
            selected: {},

            formModal: false,
        };
    },

    created()
    {
        this.initialize();
    },

    computed:
    {
        ...mapGetters({
            getNo: 'auth/getSeriesNo',
            isSet: 'auth/isSet',
        }),

        nextStartingSeries()
        {
            return parseInt(this.getNo('ending')) + 1;
        },

        nextEndingSeries()
        {
            return (parseInt(this.nextStartingSeries) + parseInt(this.getNo('total'))) - 1;
        },
    },

    methods:
    {
        ...mapActions({
            update: 'auth/updateLastRelease',
            save: 'auth/saveSeries',
        }),

        async changeToNextSeries()
        {
            let newStartingNo = parseInt(this.getNo('ending')) + 1;
            let newEndingNo = (newStartingNo + parseInt(this.getNo('total'))) - 1;
            let newTotalno = parseInt(this.getNo('total'));

            await this.save({
                starting_no: newStartingNo,
                ending_no: newEndingNo,
                total_no: newTotalno,
                last_release_no: null
            })
        },

        async changeToPreviousSeries()
        {
            let newEndingNo = parseInt(this.getNo('starting')) - 1;
            let newStartingNo = (newEndingNo - parseInt(this.getNo('total'))) + 1;
            let newTotalNo = parseInt(this.getNo('total'));

            if (newStartingNo > 0)
            {
                return await this.save({
                    starting_no: newStartingNo,
                    ending_no: newEndingNo,
                    total_no: newTotalNo,
                    last_release_no: null
                })
            }

            Toast.error({
                message: 'Oops, Starting Series No. will be out of value.',
            });
        },

        initialize()
        {
            if (!this.isSet)
                this.toggleModal('form', true);
        },

        toggleModal(name, bool)
        {
            this[`${name}Modal`] = bool;
        },
    }
};
</script>
