<template>
    <vh-modal
        :isOpen="$parent.$data.formModal"
        :isCenter="true">

        <vh-modal-body>
            <div class="vh-margin-top">
                <h2 class="axs-title vh-margin-remove">
                    Ticket Form
                </h2>

                <p class="axs-paragraph">
                    Kindly fill-out required fields appropriately.
                </p>

                <form class="vh-form-stacked"
                    @submit.prevent="submit()">
                    <fieldset class="vh-fieldset">
                        <div class="vh-margin-top">
                            <label class="axs-input-label">
                                * Starting Series No.
                            </label>
                            <input
                                type="number"
                                class="axs-input"
                                required="true"
                                autofocus="true"
                                    v-model="form.starting_no">

                            <small class="vh-text-danger vh-text-bold"
                                v-if="hasError('starting_no')">
                                {{ errors.starting_no[0] }}
                            </small>
                        </div>
                        <div class="vh-margin">
                            <label class="axs-input-label">
                                * Total Tickets
                            </label>
                            <input
                                type="number"
                                class="axs-input"
                                required="true"
                                    v-model="form.total_no">

                            <small class="vh-text-danger vh-text-bold"
                                v-if="hasError('total_no')">
                                {{ errors.total_no[0] }}
                            </small>
                        </div>
                    </fieldset>

                    <div class="vh-margin-top vh-text-center">
                        <vh-button
                            class="vh-margin-small-right"
                            type="button"
                            :size="'large'"
                            @click="closeModal()"
                            :color="'default'">
                            CLOSE
                        </vh-button>
                        <vh-button
                            type="submit"
                            :size="'large'"
                            :color="'default'">
                            💾 SAVE
                        </vh-button>
                    </div>
                </form>
            </div>  
        </vh-modal-body>
    </vh-modal>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

import { Toast, Form } from '@/utils';

export default
{
    data()
    {
        return {
            errors: {},

            form:
            {
                starting_no: 100000,
                ending_no: 10099,
                total_no: 100,
                last_release_no: null,
            }
        }
    },

    created()
    {
        this.initialize();
    },

    computed:
    {
        ...mapGetters({
            getNo: 'auth/getSeriesNo'
        }),

        selected()
        {
            return this.$parent.$data.selected;
        },

        formModal()
        {
            return this.$parent.$data.formModal;
        }
    },

    methods:
    {
        ...mapActions({
            save: 'auth/saveSeries',
            
        }),

        submit()
        {
            this.errors = {};

            if (!this.form.starting_no)
            {
                this.errors.starting_no = ['😌 Please fill this required field.'];
            }

            if (!this.form.total_no)
            {
                this.errors.total_no = ['😌 Please fill this required field.'];
            }

            if (Object.keys(this.errors).length > 0)
            {
                return Toast.error({
                    message: 'Oops, kindly double check your form.',
                });
            }

            this.form.ending_no = parseInt(this.form.starting_no) + parseInt(this.form.total_no);

            this.form.ending_no -= 1;

            if (this.form.starting_no != this.getNo('starting'))
            {
                this.form.last_release_no = null;
            }

            if (this.form.last_release_no > this.form.ending_no)
            {
                this.form.last_release_no = null;
            }

            this.save(this.form);

            this.closeModal();
        },

        initialize()
        {
            this.form.starting_no = this.getNo('starting');
            this.form.ending_no = this.getNo('ending');
            this.form.total_no = this.getNo('total');
            this.form.last_release_no = this.getNo('last_release');
        },

        hasError(name = null)
        {
            return Form.hasError(this.errors, name);
        },

        closeModal()
        {
            this.$parent.toggleModal('form', false);
        },
    },

    watch:
    {
        formModal()
        {
            this.initialize();
        }
    }
};
</script>
